body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background:-webkit-linear-gradient(left, rgb(7, 62, 90), rgb(16, 150, 120));
  background:linear-gradient(to right, rgb(7, 62, 90), rgb(16, 150, 120));
}

button {
  cursor: pointer;	
}

.App {
  text-align: center;
}

.Tilt {
  background: -webkit-linear-gradient(1deg, #1d58b7 0%, #04DEB2 100%);
  background: linear-gradient(89deg, #1d58b7 0%, #04DEB2 100%); 
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  height:40px;
 }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.form {
  width: 600px;
  height: 100px;
  align-content:'center';
  background:
  -webkit-linear-gradient(bottom, rgb(26, 138, 79), rgb(43, 153, 142));
  background:
  linear-gradient(to top, rgb(26, 138, 79), rgb(43, 153, 142));
  background-size:40px 10px;
}
.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 2px #f12213 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
#modal-root {
  position: relative;
  z-index: 999;
}
.profile-modal {
  background-color: rgba(255,255, 255,0.8);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close {
  font-size: 3rem;
  font-style: bold;
  cursor: pointer;
}

.modal-close:hover {
    color: grey;
    cursor: pointer;
}
